n  }\n  .measure-narrow-ns {\n    max-width: $measure-narrow;\n  }\n  .indent-ns {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-ns {\n    font-variant: small-caps;\n  }\n  .truncate-ns {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n\n@media #{$breakpoint-medium} {\n  .measure-m {\n    max-width: $measure;\n  }\n  .measure-wide-m {\n    max-width: $measure-wide;\n  }\n  .measure-narrow-m {\n    max-width: $measure-narrow;\n  }\n  .indent-m {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-m {\n    font-variant: small-caps;\n  }\n  .truncate-m {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n\n@media #{$breakpoint-large} {\n  .measure-l {\n    max-width: $measure;\n  }\n  .measure-wide-l {\n    max-width: $measure-wide;\n  }\n  .measure-narrow-l {\n    max-width: $measure-narrow;\n  }\n  .indent-l {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-l {\n    font-variant: small-caps;\n  }\n  .truncate-l {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   UTILITIES\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n/* Equivalent to .overflow-y-scroll */\n.overflow-container {\n  overflow-y: scroll;\n}\n\n.center {\n  margin-right: auto;\n  margin-left: auto;\n}\n\n.mr-auto { margin-right: auto; }\n.ml-auto { margin-left:  auto; }\n\n@media #{$breakpoint-not-small}{\n  .center-ns {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-ns { margin-right: auto; }\n  .ml-auto-ns { margin-left:  auto; }\n}\n\n@media #{$breakpoint-medium}{\n  .center-m {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-m { margin-right: auto; }\n  .ml-auto-m { margin-left:  auto; }\n}\n\n@media #{$breakpoint-large}{\n  .center-l {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-l { margin-right: auto; }\n  .ml-auto-l { margin-left:  auto; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   VISIBILITY\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n\n/*\n    Text that is hidden but accessible\n    Ref: http://snook.ca/archives/html_and_css/hiding-content-for-accessibility\n*/\n\n.clip {\n  position: fixed !important;\n  _position: absolute !important;\n  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n@media #{$breakpoint-not-small} {\n  .clip-ns {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n@media #{$breakpoint-medium} {\n  .clip-m {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n@media #{$breakpoint-large} {\n  .clip-l {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   WHITE SPACE\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n\n.ws-normal { white-space: normal; }\n.nowrap { white-space: nowrap; }\n.pre { white-space: pre; }\n\n@media #{$breakpoint-not-small} {\n  .ws-normal-ns { white-space: normal; }\n  .nowrap-ns { white-space: nowrap; }\n  .pre-ns { white-space: pre; }\n}\n\n@media #{$breakpoint-medium} {\n  .ws-normal-m { white-space: normal; }\n  .nowrap-m { white-space: nowrap; }\n  .pre-m { white-space: pre; }\n}\n\n@media #{$breakpoint-large} {\n  .ws-normal-l { white-space: normal; }\n  .nowrap-l { white-space: nowrap; }\n  .pre-l { white-space: pre; }\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   VERTICAL ALIGN\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n.v-base     { vertical-align: baseline; }\n.v-mid      { vertical-align: middle; }\n.v-top      { vertical-align: top; }\n.v-btm      { vertical-align: bottom; }\n\n@media #{$breakpoint-not-small} {\n  .v-base-ns     { vertical-align: baseline; }\n  .v-mid-ns      { vertical-align: middle; }\n  .v-top-ns      { vertical-align: top; }\n  .v-btm-ns      { vertical-align: bottom; }\n}\n\n@media #{$breakpoint-medium} {\n  .v-base-m     { vertical-align: baseline; }\n  .v-mid-m      { vertical-align: middle; }\n  .v-top-m      { vertical-align: top; }\n  .v-btm-m      { vertical-align: bottom; }\n}\n\n@media #{$breakpoint-large} {\n  .v-base-l     { vertical-align: baseline; }\n  .v-mid-l      { vertical-align: middle; }\n  .v-top-l      { vertical-align: top; }\n  .v-btm-l      { vertical-align: bottom; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n  HOVER EFFECTS\n  Docs: http://tachyons.io/docs/themes/hovers/\n\n    - Dim\n    - Glow\n    - Hide Child\n    - Underline text\n    - Grow\n    - Pointer\n    - Shadow\n\n*/\n\n/*\n\n  Dim element on hover by adding the dim class.\n\n*/\n.dim {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n.dim:hover,\n.dim:focus {\n  opacity: .5;\n  transition: opacity .15s ease-in;\n}\n.dim:active {\n  opacity: .8; transition: opacity .15s ease-out;\n}\n\n/*\n\n  Animate opacity to 100% on hover by adding the glow class.\n\n*/\n.glow {\n  transition: opacity .15s ease-in;\n}\n.glow:hover,\n.glow:focus {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n\n/*\n\n  Hide child & reveal on hover:\n\n  Put the hide-child class on a parent element and any nested element with the\n  child class will be hidden and displayed on hover or focus.\n\n  <div class=\"hide-child\">\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n  </div>\n*/\n\n.hide-child .child {\n  opacity: 0;\n  transition: opacity .15s ease-in;\n}\n.hide-child:hover  .child,\n.hide-child:focus  .child,\n.hide-child:active .child {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n\n.underline-hover:hover,\n.underline-hover:focus {\n  text-decoration: underline;\n}\n\n/* Can combine this with overflow-hidden to make background images grow on hover\n * even if you are using background-size: cover */\n\n.grow {\n  -moz-osx-font-smoothing: grayscale;\n  backface-visibility: hidden;\n  transform: translateZ(0);\n  transition: transform 0.25s ease-out;\n}\n\n.grow:hover,\n.grow:focus {\n  transform: scale(1.05);\n}\n\n.grow:active {\n  transform: scale(.90);\n}\n\n.grow-large {\n  -moz-osx-font-smoothing: grayscale;\n  backface-visibility: hidden;\n  transform: translateZ(0);\n  transition: transform .25s ease-in-out;\n}\n\n.grow-large:hover,\n.grow-large:focus {\n  transform: scale(1.2);\n}\n\n.grow-large:active {\n  transform: scale(.95);\n}\n\n/* Add pointer on hover */\n\n.pointer:hover {\n  cursor: pointer;\n}\n\n/*\n   Add shadow on hover.\n\n   Performant box-shadow animation pattern from\n   http://tobiasahlin.com/blog/how-to-animate-box-shadow/\n*/\n\n.shadow-hover {\n  cursor: pointer;\n  position: relative;\n  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);\n}\n\n.shadow-hover::after {\n  content: '';\n  box-shadow: 0px 0px 16px 2px rgba( 0, 0, 0, .2 );\n  border-radius: inherit;\n  opacity: 0;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: -1;\n  transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);\n}\n\n.shadow-hover:hover::after,\n.shadow-hover:focus::after {\n  opacity: 1;\n}\n\n/* Combine with classes in skins and skins-pseudo for\n * many different transition possibilities. */\n\n.bg-animate,\n.bg-animate:hover,\n.bg-animate:focus {\n  transition: background-color .15s ease-in-out;\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n  Z-INDEX\n\n  Base\n    z = z-index\n\n  Modifiers\n    -0 = literal value 0\n    -1 = literal value 1\n    -2 = literal value 2\n    -3 = literal value 3\n    -4 = literal value 4\n    -5 = literal value 5\n    -999 = literal value 999\n    -9999 = literal value 9999\n\n    -max = largest accepted z-index value as integer\n\n    -inherit = string value inherit\n    -initial = string value initial\n    -unset = string value unset\n\n  MDN: https://developer.mozilla.org/en/docs/Web/CSS/z-index\n  Spec: http://www.w3.org/TR/CSS2/zindex.html\n  Articles:\n    https://philipwalton.com/articles/what-no-one-told-you-about-z-index/\n\n  Tips on extending:\n  There might be a time worth using negative z-index values.\n  Or if you are using tachyons with another project, you might need to\n  adjust these values to suit your needs.\n\n*/\n\n.z-0 { z-index: 0; }\n.z-1 { z-index: 1; }\n.z-2 { z-index: 2; }\n.z-3 { z-index: 3; }\n.z-4 { z-index: 4; }\n.z-5 { z-index: 5; }\n\n.z-999 { z-index: 999; }\n.z-9999 { z-index: 9999; }\n\n.z-max {\n  z-index: 2147483647;\n}\n\n.z-inherit { z-index: inherit; }\n.z-initial { z-index: initial; }\n.z-unset { z-index: unset; }\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n    NESTED\n    Tachyons module for styling nested elements\n    that are generated by a cms.\n\n*/\n\n.nested-copy-line-height p,\n.nested-copy-line-height ul,\n.nested-copy-line-height ol {\n  line-height: $line-height-copy;\n}\n\n.nested-headline-line-height h1,\n.nested-headline-line-height h2,\n.nested-headline-line-height h3,\n.nested-headline-line-height h4,\n.nested-headline-line-height h5,\n.nested-headline-line-height h6 {\n  line-height: $line-height-title;\n}\n\n.nested-list-reset ul,\n.nested-list-reset ol {\n  padding-left: 0;\n  margin-left: 0;\n  list-style-type: none;\n}\n\n.nested-copy-indent p+p {\n  text-indent: $letter-spacing-1;\n  margin-top: $spacing-none;\n  margin-bottom: $spacing-none;\n}\n\n.nested-copy-seperator p+p {\n  margin-top: $spacing-copy-separator;\n}\n\n.nested-img img {\n  width: 100%;\n  max-width: 100%;\n  display: block;\n}\n\n.nested-links a {\n  color: $blue;\n  transition: color .15s ease-in;\n}\n\n.nested-links a:hover,\n.nested-links a:focus {\n  color: $light-blue;\n  transition: color .15s ease-in;\n}\n",".wrapper\n{\n    width: 100%;\n    max-width: 1460px;\n    margin: 0 auto;\n    padding: 0 20px;\n    box-sizing: border-box;\n}\n\n.opblock-tag-section\n{\n    display: flex;\n    flex-direction: column;\n}\n\n.try-out.btn-group {\n  padding: 0;\n}\n\n.opblock-tag\n{\n    display: flex;\n    align-items: center;\n\n    padding: 10px 20px 10px 10px;\n\n    cursor: pointer;\n    transition: all .2s;\n\n    border-bottom: 1px solid rgba($opblock-tag-border-bottom-color, .3);\n\n    &:hover\n    {\n        background: rgba($opblock-tag-background-color-hover,.02);\n    }\n}\n\n@mixin method($color)\n{\n    border-color: $color;\n    background: rgba($color, .1);\n\n    .opblock-summary-method\n    {\n        background: $color;\n    }\n\n    .opblock-summary\n    {\n        border-color: $color;\n    }\n\n    .tab-header .tab-item.active h4 span:after\n    {\n        background: $color;\n    }\n}\n\n\n\n\n.opblock-tag\n{\n    font-size: 24px;\n\n    margin: 0 0 5px 0;\n\n    @include text_headline();\n\n    &.no-desc\n    {\n        span\n        {\n            flex: 1;\n        }\n    }\n\n    svg\n    {\n        transition: all .4s;\n    }\n\n    small\n    {\n        font-size: 14px;\n        font-weight: normal;\n\n        flex: 1;\n\n        padding: 0 10px;\n\n        @include text_body();\n    }\n}\n\n.parameter__type\n{\n    font-size: 12px;\n\n    padding: 5px 0;\n\n    @include text_code();\n}\n\n.parameter-controls {\n    margin-top: 0.75em;\n}\n\n.examples {\n    &__title {\n        display: block;\n        font-size: 1.1em;\n        font-weight: bold;\n        margin-bottom: 0.75em;\n    }\n\n    &__section {\n        margin-top: 1.5em;\n    }\n    &__section-header {\n        font-weight: bold;\n        font-size: .9rem;\n        margin-bottom: .5rem;\n        // color: #555;\n    }\n}\n\n.examples-select {\n    margin-bottom: .75em;\n    display: inline-block;\n    .examples-select-element {\n      width: 100%;\n    }\n    &__section-label {\n        font-weight: bold;\n        font-size: .9rem;\n        margin-right: .5rem;\n    }\n}\n\n.example {\n    &__section {\n        margin-top: 1.5em;\n    }\n    &__section-header {\n        font-weight: bold;\n        font-size: .9rem;\n        margin-bottom: .5rem;\n        // color: #555;\n    }\n}\n\n.view-line-link\n{\n    position: relative;\n    top: 3px;\n\n    width: 20px;\n    margin: 0 5px;\n\n    cursor: pointer;\n    transition: all .5s;\n}\n\n\n\n.opblock\n{\n    margin: 0 0 15px 0;\n\n    border: 1px solid $opblock-border-color;\n    border-radius: 4px;\n    box-shadow: 0 0 3px rgba($opblock-box-shadow-color,.19);\n\n    .tab-header\n    {\n        display: flex;\n\n        flex: 1;\n\n        .tab-item\n        {\n            padding: 0 40px;\n\n            cursor: pointer;\n\n            &:first-of-type\n            {\n                padding: 0 40px 0 0;\n            }\n            &.active\n            {\n                h4\n                {\n                    span\n                    {\n                        position: relative;\n\n\n                        &:after\n                        {\n                            position: absolute;\n                            bottom: -15px;\n                            left: 50%;\n\n                            width: 120%;\n                            height: 4px;\n\n                            content: '';\n                            transform: translateX(-50%);\n\n                            background: $opblock-tab-header-tab-item-active-h4-span-after-background-color;\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n\n    &.is-open\n    {\n        .opblock-summary\n        {\n            border-bottom: 1px solid $opblock-isopen-summary-border-bottom-color;\n        }\n    }\n\n    .opblock-section-header\n    {\n        display: flex;\n        align-items: center;\n\n        padding: 8px 20px;\n\n        min-height: 50px;\n\n        background: rgba($opblock-isopen-section-header-background-color,.8);\n        box-shadow: 0 1px 2px rgba($opblock-isopen-section-header-box-shadow-color,.1);\n\n        >label\n        {\n            font-size: 12px;\n            font-weight: bold;\n\n            display: flex;\n            align-items: center;\n\n            margin: 0;\n            margin-left: auto;\n\n            @include text_headline();\n\n            >span\n            {\n                padding: 0 10px 0 0;\n            }\n        }\n\n        h4\n        {\n            font-size: 14px;\n\n            flex: 1;\n\n            margin: 0;\n\n            @include text_headline();\n        }\n    }\n\n    .opblock-summary-method\n    {\n        font-size: 14px;\n        font-weight: bold;\n\n        min-width: 80px;\n        padding: 6px 15px;\n\n        text-align: center;\n\n        border-radius: 3px;\n        background: $opblock-summary-method-background-color;\n        text-shadow: 0 1px 0 rgba($opblock-summary-method-text-shadow-color,.1);\n\n        @include text_headline($opblock-summary-method-font-color);\n    }\n\n    .opblock-summary-path,\n    .opblock-summary-operation-id,\n    .opblock-summary-path__deprecated\n    {\n        font-size: 16px;\n        @media (max-width: 768px) {\n          font-size: 12px;\n        }\n\n\n        display: flex;\n        align-items: center;\n\n        word-break: break-word;\n\n        padding: 0 10px;\n\n        @include text_code();\n\n    }\n\n    .opblock-summary-path\n    {\n        flex-shrink: 0;\n        max-width: calc(100% - 110px - 15rem);\n    }\n\n    .opblock-summary-path__deprecated\n    {\n        text-decoration: line-through;\n    }\n\n    .opblock-summary-operation-id\n    {\n        font-size: 14px;\n    }\n\n    .opblock-summary-description\n    {\n        font-size: 13px;\n\n        flex: 1 1 auto;\n\n        word-break: break-word;\n\n        @include text_body();\n    }\n\n    .opblock-summary\n    {\n        display: flex;\n        align-items: center;\n\n        padding: 5px;\n\n        cursor: pointer;\n\n        .view-line-link\n        {\n            position: relative;\n            top: 2px;\n\n            width: 0;\n            margin: 0;\n\n            cursor: pointer;\n            transition: all .5s;\n        }\n\n        &:hover\n        {\n            .view-line-link\n            {\n                width: 18px;\n                margin: 0 5px;\n            }\n        }\n    }\n\n\n\n    &.opblock-post\n    {\n        @include method($_color-post);\n    }\n\n    &.opblock-put\n    {\n        @include method($_color-put);\n    }\n\n    &.opblock-delete\n    {\n        @include method($_color-delete);\n    }\n\n    &.opblock-get\n    {\n        @include method($_color-get);\n    }\n\n    &.opblock-patch\n    {\n        @include method($_color-patch);\n    }\n\n    &.opblock-head\n    {\n        @include method($_color-head);\n    }\n\n    &.opblock-options\n    {\n        @include method($_color-options);\n    }\n\n    &.opblock-deprecated\n    {\n        opacity: .6;\n\n        @include method($_color-disabled);\n    }\n\n    .opblock-schemes\n    {\n        padding: 8px 20px;\n\n        .schemes-title\n        {\n            padding: 0 10px 0 0;\n        }\n    }\n}\n\n.filter\n{\n    .operation-filter-input\n    {\n        width: 100%;\n        margin: 20px 0;\n        padding: 10px 10px;\n\n        border: 2px solid $operational-filter-input-border-color;\n    }\n}\n\n.filter, .download-url-wrapper\n{\n    .failed\n    {\n        color: red;\n    }\n\n    .loading\n    {\n        color: #aaa;\n    }\n}\n\n.model-example {\n    margin-top: 1em;\n}\n\n.tab\n{\n    display: flex;\n\n    padding: 0;\n\n    list-style: none;\n\n    li\n    {\n        font-size: 12px;\n\n        min-width: 60px;\n        padding: 0;\n\n        cursor: pointer;\n\n        @include text_headline();\n\n        &:first-of-type\n        {\n            position: relative;\n\n            padding-left: 0;\n            padding-right: 12px;\n\n            &:after\n            {\n                position: absolute;\n                top: 0;\n                right: 6px;\n\n                width: 1px;\n                height: 100%;\n\n                content: '';\n\n                background: rgba($tab-list-item-first-background-color,.2);\n            }\n        }\n\n        &.active\n        {\n            font-weight: bold;\n        }\n    }\n}\n\n.opblock-description-wrapper,\n.opblock-external-docs-wrapper,\n.opblock-title_normal\n{\n    font-size: 12px;\n\n    margin: 0 0 5px 0;\n    padding: 15px 20px;\n\n    @include text_body();\n\n    h4\n    {\n        font-size: 12px;\n\n        margin: 0 0 5px 0;\n\n        @include text_body();\n    }\n\n    p\n    {\n        font-size: 14px;\n\n        margin: 0;\n\n        @include text_body();\n    }\n}\n\n.opblock-external-docs-wrapper {\n  h4 {\n    padding-left: 0px;\n  }\n}\n\n.execute-wrapper\n{\n    padding: 20px;\n\n    text-align: right;\n\n    .btn\n    {\n        width: 100%;\n        padding: 8px 40px;\n    }\n}\n\n.body-param-options\n{\n    display: flex;\n    flex-direction: column;\n\n    .body-param-edit\n    {\n        padding: 10px 0;\n    }\n\n    label\n    {\n        padding: 8px 0;\n        select\n        {\n            margin: 3px 0 0 0;\n        }\n    }\n}\n\n.responses-inner\n{\n    padding: 20px;\n\n    h5,\n    h4\n    {\n        font-size: 12px;\n\n        margin: 10px 0 5px 0;\n\n        @include text_body();\n    }\n\n    .curl\n    {\n        white-space: normal;\n    }\n}\n\n.response-col_status\n{\n    font-size: 14px;\n\n    @include text_body();\n\n    .response-undocumented\n    {\n        font-size: 11px;\n\n        @include text_code($response-col-status-undocumented-font-color);\n    }\n}\n\n.response-col_links\n{\n    padding-left: 2em;\n    max-width: 40em;\n    font-size: 14px;\n\n    @include text_body();\n\n    .response-undocumented\n    {\n        font-size: 11px;\n\n        @include text_code($response-col-links-font-color);\n    }\n\n    .operation-link\n    {\n        margin-bottom: 1.5em;\n\n        .description\n        {\n            margin-bottom: 0.5em;\n        }\n    }\n}\n\n.opblock-body\n{\n  .opblock-loading-animation\n  {\n    display: block;\n    margin: 3em;\n    margin-left: auto;\n    margin-right: auto;\n  }\n}\n\n.opblock-body pre.microlight\n{\n    font-size: 12px;\n\n    margin: 0;\n    padding: 10px;\n\n    white-space: pre-wrap;\n    word-wrap: break-word;\n    word-break: break-all;\n    word-break: break-word;\n    hyphens: auto;\n\n    border-radius: 4px;\n    background: $opblock-body-background-color;\n\n    overflow-wrap: break-word;\n    @include text_code($opblock-body-font-color);\n\n    // disabled to have syntax highliting with react-syntax-highlight\n    // span\n    // {\n    //     color: $opblock-body-font-color !important;\n    // }\n\n    .headerline\n    {\n        display: block;\n    }\n}\n\n.highlight-code {\n  position: relative;\n\n  > .microlight {\n    overflow-y: auto;\n    max-height: 400px;\n    min-height: 6em;\n\n    code {\n        white-space: pre-wrap !important;\n        word-break: break-all;\n    }\n  }\n}\n.curl-command {\n  position: relative;\n}\n\n.download-contents {\n  position: absolute;\n  bottom: 10px;\n  right: 10px;\n  cursor: pointer;\n  background: #7d8293;\n  text-align: center;\n  padding: 5px;\n  border-radius: 4px;\n  font-family: sans-serif;\n  font-weight: 600;\n  color: white;\n  font-size: 14px;\n  height: 30px;\n  width: 75px;\n}\n\n.scheme-container\n{\n    margin: 0 0 20px 0;\n    padding: 30px 0;\n\n    background: $scheme-container-background-color;\n    box-shadow: 0 1px 2px 0 rgba($scheme-container-box-shadow-color,.15);\n\n    .schemes\n    {\n        display: flex;\n        align-items: flex-end;\n\n         > label\n        {\n            font-size: 12px;\n            font-weight: bold;\n\n            display: flex;\n            flex-direction: column;\n\n            margin: -20px 15px 0 0;\n\n            @include text_headline();\n\n            select\n            {\n                min-width: 130px;\n\n                text-transform: uppercase;\n            }\n        }\n    }\n}\n\n.loading-container\n{\n    padding: 40px 0 60px;\n    margin-top: 1em;\n    min-height: 1px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n\n    .loading\n    {\n        position: relative;\n\n\n        &:after\n        {\n            font-size: 10px;\n            font-weight: bold;\n\n            position: absolute;\n            top: 50%;\n            left: 50%;\n\n            content: 'loading';\n            transform: translate(-50%,-50%);\n            text-transform: uppercase;\n\n            @include text_headline();\n        }\n\n        &:before\n        {\n            position: absolute;\n            top: 50%;\n            left: 50%;\n\n            display: block;\n\n            width: 60px;\n            height: 60px;\n            margin: -30px -30px;\n\n            content: '';\n            animation: rotation 1s infinite linear, opacity .5s;\n\n            opacity: 1;\n            border: 2px solid rgba($loading-container-before-border-color, .1);\n            border-top-color: rgba($loading-container-before-border-top-color, .6);\n            border-radius: 100%;\n\n            backface-visibility: hidden;\n\n            @keyframes rotation\n            {\n                to\n                {\n                    transform: rotate(360deg);\n                }\n            }\n        }\n    }\n}\n\n.response-controls {\n    padding-top: 1em;\n    display: flex;\n}\n\n.response-control-media-type {\n    margin-right: 1em;\n\n    &--accept-controller {\n        select {\n            border-color: $response-content-type-controls-accept-header-select-border-color;\n        }\n    }\n\n    &__accept-message {\n        color: $response-content-type-controls-accept-header-small-font-color;\n        font-size: .7em;\n    }\n\n    &__title {\n        display: block;\n        margin-bottom: 0.2em;\n        font-size: .7em;\n    }\n}\n\n.response-control-examples {\n    &__title {\n        display: block;\n        margin-bottom: 0.2em;\n        font-size: .7em;\n    }\n}\n\n@keyframes blinker\n{\n    50%\n    {\n        opacity: 0;\n    }\n}\n\n.hidden\n{\n    display: none;\n}\n\n.no-margin\n{\n    height: auto;\n    border: none;\n    margin: 0;\n    padding: 0;\n}\n\n.float-right\n{\n    float: right;\n}\n\nimg.full-width\n{\n    width: 100%;\n}\n\n.svg-assets\n{\n    position: absolute;\n    width: 0;\n    height: 0;\n}\n\nsection\n{\n    h3\n    {\n        @include text_headline();\n    }\n}\n\na.nostyle {\n  text-decoration: inherit;\n  color: inherit;\n  cursor: pointer;\n  display: inline;\n\n  &:visited {\n    text-decoration: inherit;\n    color: inherit;\n    cursor: pointer;\n  }\n}\n\n.fallback\n{\n    padding: 1em;\n    color: #aaa;\n}\n\n.version-pragma {\n  height: 100%;\n  padding: 5em 0px;\n\n  &__message {\n    display: flex;\n    justify-content: center;\n    height: 100%;\n    font-size: 1.2em;\n    text-align: center;\n    line-height: 1.5em;\n\n    padding: 0px .6em;\n\n    > div {\n      max-width: 55ch;\n      flex: 1;\n    }\n\n    code {\n      background-color: #dedede;\n      padding: 4px 4px 2px;\n      white-space: pre;\n    }\n  }\n}\n\n.opblock-link\n{\n    font-weight: normal;\n\n    &.shown\n    {\n        font-weight: bold;\n    }\n}\n\nspan\n{\n    &.token-string\n    {\n        color: #555;\n    }\n\n    &.token-not-formatted\n    {\n        color: #555;\n        font-weight: bold;\n    }\n}\n","// Base Colours\n$black: #000 !default;\n$white: #fff !default;\n$gray-50: lighten($black, 92%) !default; //ebebeb\n$gray-200: lighten($black, 62.75%) !default; // #a0a0a0\n$gray-300: lighten($black, 56.5%) !default; // #909090\n$gray-400: lighten($black, 50%) !default; // #808080\n$gray-500: lighten($black, 43.75%) !default; // #707070\n$gray-600: lighten($black, 37.5%) !default; // #606060\n$gray-650: lighten($black, 33.3%) !default; // #555555\n$gray-700: lighten($black, 31.25%) !default; // #505050\n$gray-800: lighten($black, 25%) !default; // #404040\n$gray-900: lighten($black, 18.75%) !default; // #303030\n\n$cod-gray: #1b1b1b !default;\n$agate-gray: #333333 !default;\n$bright-gray: #3b4151 !default;\n$mako-gray: #41444e !default;\n$waterloo-gray: #7d8492 !default;\n$alto-gray: #d9d9d9 !default;\n$mercury-gray: #e4e4e4 !default;\n$concrete-gray: #e8e8e8 !default;\n$alabaster: #f7f7f7 !default;\n$apple-green: #62a03f !default;\n$green-haze: #009d77 !default;\n$japanese-laurel: #008000 !default;\n$persian-green: #00a0a7 !default;\n$geyser-blue: #d8dde7 !default;\n$dodger-blue: #1391ff !default;\n$endeavour-blue: #005dae !default;\n$scampi-purple: #55a !default;\n$electric-violet: #7300e5 !default;\n$persian-red: #cf3030 !default;\n$mango-tango: #e97500 !default;\n\n// Theme\n\n$color-primary: #89bf04 !default;\n$color-secondary: #9012fe !default;\n$color-info: #4990e2 !default;\n$color-warning: #ff6060 !default;\n$color-danger: #f00 !default;\n\n$color-primary-hover: lighten($color-primary, .5%) !default;\n\n$_color-post: #49cc90 !default;\n$_color-get: #61affe !default;\n$_color-put: #fca130 !default;\n$_color-delete: #f93e3e !default;\n$_color-head: #9012fe !default;\n$_color-patch: #50e3c2 !default;\n$_color-disabled: #ebebeb !default;\n$_color-options: #0d5aa7 !default;\n\n// Authorize\n\n$auth-container-border-color: $gray-50 !default;\n$auth-select-all-none-link-font-color: $color-info !default;\n// Buttons\n\n$btn-background-color: transparent !default;\n$btn-border-color: $gray-400 !default;\n$btn-font-color: inherit !default;\n$btn-box-shadow-color: $black !default;\n\n$btn-authorize-background-color: transparent !default;\n$btn-authorize-border-color: $_color-post !default;\n$btn-authorize-font-color: $_color-post !default;\n$btn-authorize-svg-fill-color: $_color-post !default;\n\n$btn-cancel-background-color: transparent !default;\n$btn-cancel-border-color: $color-warning !default;\n$btn-cancel-font-color: $color-warning !default;\n\n$btn-execute-background-color: transparent !default;\n$btn-execute-border-color: $color-info !default;\n$btn-execute-font-color: $white !default;\n$btn-execute-background-color-alt: $color-info !default;\n\n$expand-methods-svg-fill-color: $gray-500 !default;\n$expand-methods-svg-fill-color-hover: $gray-800 !default;\n\n// Errors\n\n$errors-wrapper-background-color: $_color-delete !default;\n$errors-wrapper-border-color: $_color-delete !default;\n\n$errors-wrapper-errors-small-font-color: $gray-600 !default;\n\n// Form\n\n$form-select-background-color: $alabaster !default;\n$form-select-border-color: $mako-gray !default;\n$form-select-box-shadow-color: $black !default;\n\n$form-input-border-color: $alto-gray !default;\n$form-input-background-color: $white !default;\n\n$form-textarea-background-color: $white !default;\n$form-textarea-focus-border-color: $_color-get !default;\n\n$form-textarea-curl-background-color: $mako-gray !default;\n$form-textarea-curl-font-color: $white !default;\n\n$form-checkbox-label-font-color: $gray-900 !default;\n$form-checkbox-background-color: $concrete-gray !default;\n$form-checkbox-box-shadow-color: $concrete-gray !default;\n\n// Information\n\n$info-code-background-color: $black !default;\n$info-c